.no-feedback_wrapper {
  background-color: var(--White);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px;
  border-radius: 10px;
}
.no_feedback_logo {
  width: 150px;
  height: 150px;
  background: url("./../../img/empty_feedback.png") center no-repeat;
  background-size: cover;
}
.no_feedback_mainblock {
}
.no_feedback_title {
  margin-top: 50px;
  font-family: "Jost";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;

  text-align: center;
  letter-spacing: -0.333333px;

  color: #3a4374;
}
.no_feedback_text {
  margin-top: 20px;
  font-family: "Jost";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 23px;
  text-align: center;
  margin-bottom: 20px;
  color: #647196;
}
